<template>
  <div class="content">
    <section class="topTime">
      <div class="weekLine">
        <div
          class="week"
          v-for="(item,index) in getWeek"
          :key="index"
          @click="selectWeek(item.week)"
        >
          <div>{{item.week}}</div>
          <div :class="item.active ? 'actived':''">{{item.day}}</div>
        </div>
      </div>
    </section>
    <section class="workContent">
      <div class="work">
        <div class="workLine" v-for="(item,index) in timeInterval" :key="index">
          <p>{{item}}</p>
          <p>内容</p>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
export default {
  data() {
    return {
      getWeek: this.getWeek,
      timeInterval: this.timeInterval
    };
  },
  mounted() {
    this.getWeekJson();
    this.getTimeInterval();
  },
  methods: {
    getWeekJson() {
      var getWeek = [];
      var currentWeek = new Date().getDate();
      var weekJson = this.util.getWeek();
      var week = ["日", "一", "二", "三", "四", "五", "六"];
      week.forEach((el, index) => {
        if (getWeek.length < 7) {
          if (weekJson[index].split("/")[2] == currentWeek) {
            getWeek.push({
              day: weekJson[index].split("/")[2],
              week: el,
              active: true
            });
          } else {
            getWeek.push({
              day: weekJson[index].split("/")[2],
              week: el,
              active: false
            });
          }
        }
      });
      this.getWeek = getWeek;
    },
    getTimeInterval() {
      var timeInterval = [];
      for (var i = 0; i < 24; i++) {
        if (i < 10) {
          timeInterval.push("0" + i + ":00");
        } else {
          timeInterval.push(i + ":00");
        }
      }
      this.timeInterval = timeInterval;
    },
    selectWeek(week) {
      this.getWeek.forEach(el => {
        if (el.week === week) {
          el.active = true;
        } else {
          el.active = false;
        }
      });
    }
  }
};
</script>

<style lang="scss" scoped>
.content {
  overflow: hidden;
}

.topTime {
  box-shadow: 0px 3px 8px $F5;
  z-index: 2;
  position: absolute;
  width: 100%;
  background-color: $fc;
  .weekLine {
    @include fr(space-around);
    .week:first-child {
      div {
        @include sc(0.95rem, $Tline);
      }
    }
    .week:last-child {
      div {
        @include sc(0.95rem, $Tline);
      }
    }
    .week {
      flex: 1;
      margin: 0.8rem 0rem;
      @include sc(0.95rem, $fColor);
      @include fc(center);
      div:first-child {
        margin-bottom: 0.5rem;
        @include sc(0.8rem, $fColor);
      }
      div:last-child {
        font-weight: bold;
        line-height: 1.8rem;
      }
      .actived {
        @include wh(1.8rem, 1.8rem);
        @include sc(0.95rem, $fc);
        @include fr(center);
        line-height: 1.8rem;
        border-radius: 5rem;
        background-color: $orange;
        color: $fc !important;
      }
    }
  }
}

.workContent {
  position: relative;
  overflow: scroll;
  height: 100%;
  width: 100%;
  .work {
    margin-top: 5.5rem;
    @include wh(100%, 100%);
    position: absolute;
    padding: 0rem 0.5rem;
    .workLine {
      @include fr(flex-start);
      padding: 0.8rem 0.5rem;
      border-bottom: 0.02rem solid $F5;
      background-color: $fc;
      @include sc(0.9rem,$fColor);
      p:first-child {
        margin: 0rem 1.5rem 0rem 0.5rem;
      }
    }
  }
}
</style>